import {getRequest} from '@/utils/api.js'
import {postRequest} from '@/utils/api.js'
import {deleteRequest} from '@/utils/api.js'
import {putRequest} from '@/utils/api.js'
import {downloadRequest} from '@/utils/download.js'
export default {
  data(){
    return {
      emps:[],
      input:'',
      loading:false,
      total:0,
      currentPage:1,
      size:10,
      empName:'',
      dialogVisible:false,
      importDataBtnText:'导入数据',
      importDataBtnIcon:'el-icon-upload2',
      importDataBtnDisabled:false,
      headers:{
        Authorization:window.sessionStorage.getItem('tokenStr')
      }
    }
  },
  methods:{
    onSuccess(){
      this.importDataBtnText='导入数据'
      this.importDataBtnIcon='el-icon-upload2'
      this.importDataBtnDisabled=false
      this.initEmps()
    },
    onError(){
      this.importDataBtnText='导入数据'
      this.importDataBtnIcon='el-icon-upload2'
      this.$message.error('导入失败')
      this.importDataBtnDisabled=false
    },
    beforeUpload(){
      this.importDataBtnIcon='el-icon-loading'
      this.importDataBtnText='正在导入'
      this.importDataBtnDisabled=true
    },
    initEmps(){
      this.loading = true
      getRequest('/employee/basic?currentPage='+this.currentPage+'&size='+this.size
      +'&name='+this.empName).then(resp=>{
        console.log(this.currentPage);
        console.log(this.total);
        this.loading = false
        if(resp){
          this.emps = resp.data
          this.total = resp.total
          console.log(resp.total);
          console.log(this.total);
        }
      })
    },
    currentChange(current){
      this.currentPage = current;
      this.initEmps()
    },
    sizeChange(size){
      this.size = size
      this.initEmps()
    },
    addEmp(){
      this.dialogVisible = true
    },
    exportData(){
      downloadRequest('/employee/basic/export');
    },
    tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      },
  },
  mounted(){
    this.initEmps()
  },
  name: "EmpBasic"
}
